body{
    background: #1f1324;
}

.container{
    width: 450px;
    margin: auto;
    text-align: center;
    background-color: black;
    box-shadow: 0px 0px 4px 3px rgb(196 , 196 , 196);
    padding: 44px;
    border-radius: 4px;
    margin-top: 55px;
    font-family: "Roboto" , sans-serif;
}

.container .lap-list{
    background: #e5d5c1;
    margin-top: 3%;
    width: 97%;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: 36%;
    font-size: 21px;
    align-items: center;
    border: none;
}

.container h1{
    font-size: 54px;
    position: absolute ;
    top: 11%;
    color: #e5d5c1;
    margin-left: 7%;
    text-align: center;
}

.container button{
    position: relative;
    margin-top: 25%;
    margin-right: 12px;
    width: 100px;
    height: 36px;
    font-size: 21px;
    border: none;
    border-radius: 4px;
    color: white;
    display: inline-block;
    align-items: center;
    justify-content: space-around;
    /* margin-inline:11px; */
    cursor: pointer;
}

.start{
    background-color: rgb(5 , 231 , 5);
}

.stop{
    background-color: rgb(250 , 16 , 16);
}

.reset{
    background-color: rgb(250 , 185 , 8);
}

.lap{
    background-color: #2aabd9;
}